*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  text-rendering: optimizeSpeed;
}

img,
picture,
svg {
  max-width: 100%;
  display: block;
}

button {
  font: inherit;
}

.container {
  width: min(68.5rem, 100% - 2rem);
  margin-inline: auto;
}

.container--wide {
  width: min(82.5rem, 100% - 2rem);
  margin-inline: auto;
}

